import React from "react";
import { Container, Row, Col, Card } from "react-bootstrap";

const WhiteLabelLaunch = () => {
  return (
    <section className="wlabel_space_layout">
      <Container>
        <Row className="rg_50">
          <Col md={11} className="mx-auto">
            <h2 className="text-center">
              Why Should You Launch a Crypto Exchange Using Our White-Label
              Cryptocurrency Exchange Script?
            </h2>
          </Col>
        </Row>
        <Row className="rg_50 wlabel_launch_row">
          <Col md={12}>
            <Card
              className="wlabel_launch_card"
              data-aos="fade-right"
              data-aos-delay="500"
            >
              <Card.Header style={{ backgroundColor: "#7534fc4d" }}>
                Step 01
              </Card.Header>
              <Card.Body>
                <div className="wlabel_launch_title">Lightspeed Launch</div>
                <div className="wlabel_launch_description">
                  Forget the multi-year software development cycles. Our
                  White-label crypto exchange gets your dream trading platform
                  launched in weeks, letting you seize hot crypto trends before
                  they cool off!
                </div>
              </Card.Body>
            </Card>
          </Col>
          <Col md={12}>
            <Card
              className="wlabel_launch_card ms-md-auto"
              data-aos="fade-left"
              data-aos-delay="500"
            >
              <Card.Header style={{ backgroundColor: "#f7dfe6" }}>
                Step 02
              </Card.Header>
              <Card.Body>
                <div className="wlabel_launch_title">Reduced Costs</div>
                <div className="wlabel_launch_description">
                  Developing an exchange platform requires a team of skilled
                  developers, experts, and designers. Our script offers a
                  cost-effective alternative, eliminating the need for extensive
                  in-house development and minimizing upfront investment.
                </div>
              </Card.Body>
            </Card>
          </Col>
          <Col md={12}>
            <Card
              className="wlabel_launch_card"
              data-aos="fade-right"
              data-aos-delay="500"
            >
              <Card.Header style={{ backgroundColor: "#C4E8CC" }}>
                Step 03
              </Card.Header>
              <Card.Body>
                <div className="wlabel_launch_title">
                  Fewer Regulatory Obligations
                </div>
                <div className="wlabel_launch_description">
                  Our White-label crypto exchange software is pre-integrated
                  with KYC/AML (Know Your Customer/Anti-Money Laundering)
                  procedures. This helps ensure compliance with relevant
                  regulations and fosters trust among your users.
                </div>
              </Card.Body>
            </Card>
          </Col>
          <Col md={12}>
            <Card
              className="wlabel_launch_card ms-md-auto"
              data-aos="fade-left"
              data-aos-delay="500"
            >
              <Card.Header style={{ backgroundColor: "#ffffbf" }}>
                Step 04
              </Card.Header>
              <Card.Body>
                <div className="wlabel_launch_title">
                  Effortless Customization
                </div>
                <div className="wlabel_launch_description">
                  Despite being pre-built, our white-label crypto exchange
                  offers a high degree of customization. You can personalize the
                  platform with the features you want to include.
                </div>
              </Card.Body>
            </Card>
          </Col>
          <Col md={12}>
            <Card
              className="wlabel_launch_card"
              data-aos="fade-right"
              data-aos-delay="500"
            >
              <Card.Header style={{ backgroundColor: "#7534fc4d" }}>
                Step 05
              </Card.Header>
              <Card.Body>
                <div className="wlabel_launch_title">Built to Scale</div>
                <div className="wlabel_launch_description">
                  Our white-label script has been designed to handle increasing
                  user traffic and trading activity while also providing
                  top-notch security to protect user assets and keep hackers at
                  bay.
                </div>
              </Card.Body>
            </Card>
          </Col>
        </Row>
      </Container>
    </section>
  );
};

export default WhiteLabelLaunch;
