import React from "react";
import { Container, Row, Col } from "react-bootstrap";
import TalkToUsNew from "../../components/common/talk-to-us-new";
import { Link } from "gatsby";
import { StaticImage } from "gatsby-plugin-image";
// import img from "../../styles/assets/images/whitelabel-crypto/whitelabel.png";
// import "./wlabel.css";
import CtaBtn from "../../components/common/contactbuttontwo";
import Connect from "../../components/common/connect-button";
import WhitelabelFeature from "./whitelabel-feature";
import WhiteLabelLaunch from "./whitelabel-launch";

const WhiteLabelCryptoScreen = (props) => {
  const { db } = props;
  return (
    <main>
      <section className="wlabel_hero_wrapper">
        <Container>
          <Row>
            <Col
              lg={7}
              className="d-flex align-items-center justify-content-center"
            >
              <div className="wlabel_lhs">
                <h1>White-Label Crypto Exchange Development</h1>
                <p>
                  Leverage our robust White-label crypto exchange solution to
                  establish a branded presence in the rapidly expanding crypto
                  landscape.
                </p>
                <div className="decenter_hero_sec_btn">
                  <Link to="/contact-us" className="demo_btn">
                    View Live Demo
                  </Link>
                  <button type="submit" className="consult_btn">
                    <TalkToUsNew label="Free Consult" db={db} />
                  </button>
                </div>
              </div>
            </Col>
            <Col lg={5}>
              <StaticImage
                width={500}
                src="../../styles/assets/images/whitelabel-crypto/whitelabel.png"
                formats={["auto", "webp", "png"]}
              />
            </Col>
          </Row>
        </Container>
      </section>
      <section className="wlabel_space_layout">
        <Container>
          <Row className="mb_50">
            <Col xl={10} xxl={9} className="mx-auto">
              <h2 className="text-center">
                Accelerate Your Entry into Cryptoverse With White-Label Crypto
                Exchange Script
              </h2>
            </Col>
          </Row>
          <Row className="rg_30 mb_30">
            <Col lg={5} className="col_flex order-lg-2">
              <StaticImage
                width={400}
                src="../../styles/assets/images/whitelabel-crypto/bitcoin_fall.png"
                formats={["auto", "webp", "png"]}
                loading="lazy"
              />
            </Col>
            <Col
              lg={7}
              className="p-lg-0 d-flex align-items-center justify-content-cente"
            >
              <div className="wlabel_script_lhs">
                <p>
                  The cryptocurrency world has seen a dramatic shift since the
                  launch of the first exchange, BitcoinMarket, on March 17th,
                  2010. Fast forward to today, just over 14 years later, there
                  are hundreds of exchanges catering to millions of users
                  worldwide. Leading platforms like Binance offer over 350
                  cryptocurrencies and handle billions of dollars in daily
                  trading. This growth reflects a historic transformation. Just
                  recently, on March 14th, 2024, Bitcoin shattered its all-time
                  high, reaching a record-breaking value of over $73,000. This
                  surge highlights the ever-increasing global interest in
                  cryptocurrencies and with it, a growing need for secure and
                  efficient crypto exchange platforms.
                </p>
              </div>
            </Col>
          </Row>
          <Row className="rg_30">
            <Col lg={5}>
              <StaticImage
                width={450}
                src="../../styles/assets/images/whitelabel-crypto/bitcoin.png"
                formats={["auto", "webp", "png"]}
                loading="lazy"
              />
            </Col>
            <Col
              lg={7}
              className="d-flex align-items-center justify-content-center"
            >
              <div className="wlabel_script_lhs">
                <p>
                  But capitalizing on this booming market can be a challenge.
                  Building a secure and feature-rich crypto exchange from
                  scratch is a huge task that requires a significant amount of
                  time, resources, and a deep well of expertise. This is where
                  our White-label crypto exchange scripts come in. These
                  customizable scripts get your crypto exchange up and running
                  in weeks, offering a fast and effective way to make your mark
                  in the exciting world of crypto. By offering a user-friendly
                  and reliable crypto trading platform, you can unlock a new
                  revenue stream and become a key player in the ever-expanding
                  crypto arena. Don't just watch the crypto market boom, be a
                  part of it. Contact us today for a free consultation.
                </p>
              </div>
            </Col>
            <Col lg={12}>
              <div className="crypto_btns_grp justify-content-center">
                <Link className="prime_submit_btn" to="/contact-us">
                  View Live Demo
                </Link>
                <button className="second_consult_btn" type="button">
                  <TalkToUsNew label="Free Consult" db={db} />
                </button>
              </div>
            </Col>
          </Row>
        </Container>
      </section>
      <section>
        <CtaBtn
          dynamicHeaderText="Be Market-Ready in Weeks. Start Building Your <span class='d-xl-block'>Crypto Exchange with Justtry Today!</span>
"
          db={db}
        />
      </section>
      <section className="wlabel_space_solution">
        <Container>
          <Row className="wlabel_solution_wrapper">
            <Col xl={11}>
              <h2>
                White-Label Crypto Exchange Software- Your One-Stop Solution
              </h2>
            </Col>
            <Col md={12}>
              <p>
                Let’s imagine that you want to open your own electronics store.
                But instead of building everything from scratch, you purchase a
                pre-built store that already has all the essential display
                shelves, checkout counters, and even basic lighting. This
                pre-built store allows you to customize anything you want and
                get the store open for business in a matter of days. That's the
                basic idea behind White-label crypto exchange software. Any
                given cryptocurrency exchange is a platform where users can buy,
                sell and trade cryptocurrencies. Building such a platform from
                the ground up requires significant expertise in multiple areas.
                It is also a complex and time-consuming process. Here’s where
                our White-label cryptocurrency exchange platform comes in handy.
                It is a pre-developed software solution that acts as the basis
                for your exchange. It offers all the core functionalities like
                order matching, user wallets, and trading charts and allows you
                to customize the software as required.
              </p>
            </Col>
            <Col md={12}>
              <Connect justifyContentPosition="connect_center" />
            </Col>
          </Row>
        </Container>
      </section>
      <WhitelabelFeature />
      <section className="wlabel_space_layout wlabel_works">
        <Container>
          <Row className="rg_30">
            <Col xl={8} className="mx-auto">
              <h2 className="text-white text-center">
                How Does Our White-Label Crypto Exchange Solution Work?
              </h2>
            </Col>

            <Col md={12}>
              <div className="wlabel_works_lhs text-center">
                <p className="text-white">
                  First, the new users create accounts on our White-label crypto
                  exchange platform, providing basic information and completing
                  any required Know Your Customer (KYC) procedures. KYC helps
                  ensure user legitimacy and compliance with regulations. Once
                  registered, users can deposit funds into their secure wallets
                  on the platform. Our script integrates with various payment
                  gateways, allowing users to deposit funds using their
                  preferred method, be it fiat currency (traditional money) or
                  existing cryptocurrency holdings. The user interface has been
                  designed with ease of use in mind. Users can find a clear
                  layout with dedicated sections for buying, selling, and
                  viewing their crypto holdings. The platform allows users to
                  place buy or sell orders for their chosen cryptocurrency. All
                  the user needs to do is specify the amount of crypto he/she
                  wishes to buy and the desired price.
                </p>
                <StaticImage
                  className="wlabel_image"
                  src="../../styles/assets/images/whitelabel-crypto/token.jpg"
                  formats={["auto", "webp", "png"]}
                />
                <p className="text-white">
                  Our powerful order-matching engine then works its magic,
                  searching for users with opposing orders (someone selling what
                  the user wants to buy, and vice versa). Once a matching order
                  is found, the trade is executed seamlessly. Our White-label
                  crypto exchange script also provides a dedicated section for
                  user profiles. Here, users can view their current crypto
                  holdings and track the performance of their trades. When the
                  user is ready to cash out the crypto holdings or transfer them
                  to another wallet, the withdrawal process is just as
                  straightforward. The user simply needs to select the desired
                  cryptocurrency and amount he/she wishes to withdraw and choose
                  the preferred withdrawal method. Our secure system will
                  process the request, allowing the user to receive the funds
                  quickly and conveniently.
                </p>

                <div className="decenter_hero_sec_btn justify-content-center">
                  <Link to="/contact-us" className="demo_btn">
                    View Live Demo
                  </Link>
                  <button type="submit" className="consult_btn">
                    <TalkToUsNew label="Free Consult" db={db} />
                  </button>
                </div>
              </div>
            </Col>
          </Row>
        </Container>
      </section>
      <WhiteLabelLaunch />
      <section className="wlabel_space_layout">
        <Container>
          <Row className="rg_30">
            <Col lg={12}>
              <h2 className="text-center">
                Why Choose Justtry For{" "}
                <span className="d-xl-block">
                  White-Label Crypto Exchange Development?
                </span>
              </h2>
            </Col>
            <Col md={7}>
              <div className="wlabel_choose_lhs">
                <p>
                  The crypto market is booming like never before. However,
                  building your exchange platform from scratch can be
                  overwhelming. That's where Justtry, a leading White-label
                  crypto exchange development company, comes in. Backed by a
                  team of passionate developers, both young talents and seasoned
                  experts, your exchange stays ahead of industry trends. Our
                  script isn't just built, it's future-proofed. This guarantees
                  access to the latest features and technology, keeping your
                  crypto exchange ahead of the curve. Choosing Justtry’s
                  White-label solution is more than just getting another
                  software. It's about acquiring a reliable partner for your
                  crypto exchange journey. Don't just enter the cryptoverse,
                  conquer it with Justtry!
                </p>
                <Connect justifyContentPosition="connect_start" />
              </div>
            </Col>
            <Col
              md={5}
              className="p-lg-0 d-flex justify-content-center align-items-center"
            >
              <StaticImage
                width={500}
                src="../../styles/assets/images/whitelabel-crypto/why-choose_white.png"
                formats={["auto", "webp", "png"]}
              />
            </Col>
          </Row>
        </Container>
      </section>
    </main>
  );
};

export default WhiteLabelCryptoScreen;
